
.tips {
  font-size: 14px;
  color: #fff;
  margin-bottom: 5px;
  text-align: center;
}
.login-container {
@include relative;
  z-index: 10000;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #2d3a4b;
}
.login-container .login-form {
  width: 500px;
}
.login-container input {
  background: transparent;
  border: 0px;
  border-radius: 0px;
  padding: 12px 5px 12px 15px;
  color: #eeeeee;
  height: 47px;
}
.login-container .el-input {
  display: inline-block;
  height: 47px;
  width: 85%;
}
.login-container .svg-container {
  padding: 6px 5px 6px 15px;
  color: #889aa4;
}
.login-container .title {
  font-size: 26px;
  font-weight: 400;
  color: #eeeeee;
  margin: 0px auto 40px auto;
  text-align: center;
  font-weight: bold;
}
.login-container .login-form {
  position: absolute;
  left: 0;
  right: 0;
  width: 400px;
  padding: 35px 35px 15px 35px;
  margin: 120px auto;
}
.login-container .el-form-item {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #454545;
}
.login-container .forget-pwd {
  color: #fff;
}



