
#exTable {
  height: 600px;
  min-width: 1080px;
  background: #e5e5e5;
  padding-top: 20px;
  border-radius: 10px;
}
#exTable a {
  color: cornflowerblue;
}
#exTable td {
  text-align: center;
}
#exTable .el-input {
  width: 200px;
}
#exTable ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
#exTable #form {
  text-align: center;
  margin-top: 40px;
}
#exTable p {
  text-align: center;
  margin-top: 40px;
}
#exTable #tableul {
  margin: 0 auto;
  width: 1080px;
  overflow: hidden;
  margin-top: 40px;
}
#exTable select {
  border-radius: 4px;
  background: #20a0ff;
  height: 36px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-right: 10px;
}
#exTable select option {
  height: 36px;
  line-height: 36px;
  background: #fff;
}
#exTable #tablenew ul {
  float: left;
  width: 180px;
  border-bottom: 1px solid #000;
}
#exTable #tablenew ul:last-of-type li {
  border-right: 1px solid #000;
}
#exTable #tablenew ul li {
  height: 30px;
  line-height: 30px;
  width: 180px;
  border: 1px solid #000;
  border-right: 0;
  border-bottom: 0;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



