
a {
  cursor: pointer;
}
.clearFix:after {
  content: ".";
  overflow: hidden;
  visibility: hidden;
  height: 0;
  clear: both;
  display: block;
}
.el-menu-vertical-demo {
  width: 180px;
  background: #324157;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1001;
  overflow: hidden;
  -webkit-transition: all .28s ease-out;
  transition: all .28s ease-out;
  text-align: left;
}
.content {
  margin-left: -40px;
}
.nav {
  margin-bottom: 30px;
  margin-top: -10px;
}
.nav button {
  padding: 15px 3%;
  border-radius: 8px;
  border: 0;
  margin-left: 5.5%;
  margin-right: 5.5%;
}
.nav button:nth-of-type(1) {
  background: #2c3e50;
}
.nav button:nth-of-type(2) {
  background: #3A71A8;
}
.nav button:nth-of-type(3) {
  background: lightskyblue;
}
.nav button:nth-of-type(4) {
  background: #30B08F;
}
.nav button:nth-of-type(5) {
  background: indianred;
}
.nav button:nth-of-type(6) {
  background: gray;
  margin-right: 0;
}
.section {
  padding-left: 50px;
}



